.Output{
    grid-column:1;
    grid-row: 2/10;
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-content: center;
  }

  .top-bar{
      display: flex;
      flex-direction: row;
      justify-content:space-between;
  }



  .edu-div, .top-info{
    display:flex;
    flex-direction: row;
    gap: 10px;  
    align-items: flex-start;
  }

  .edu-info, .address{
      display:flex;
      flex-direction: column;
      width: 100%;
      align-items: flex-start;
  }